import './PageContent.scss';
import React, { useEffect, useState } from 'react';
import * as usersApi from '../../api/api';
// import { Header } from '../Header';
// import { MainInfo } from '../MainInfo';
import { GetRequest } from '../GetRequest';
import { PostRequest } from '../PostRequest';

export function PageContent() {
  const [users, setUsers] = useState<User[]>([]);
  const [nextLink, setNextLink] = useState('');

  useEffect(() => {
    usersApi.getUsers(1)
      .then((firstUsers) => {
        return (
          setUsers(firstUsers.users),
          setNextLink(firstUsers.links.next_url)
        );
      });
  }, [users]);

  const onClick = () => {
    usersApi.addUsers(nextLink)
      .then((newUsers) => {
        try {
          return (
            setUsers([...users, ...newUsers.users]),
            setNextLink(newUsers.links.next_url)
          );
        } catch (error) {
          return (
            setNextLink('')
          );
        }
      });
  };

  return (
    <div className="page-content">
      {/* <Header />
      <MainInfo /> */}
      <GetRequest users={users} handleClick={onClick} nextLink={nextLink} />
      <PostRequest />
    </div>
  );
}

export default PageContent;
