/* eslint-disable react/function-component-definition */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { useEffect, useState } from 'react';
import './PostRequest.scss';
import * as usersApi from '../../api/api';
import newImage from '../../images/success-image.svg';

export const PostRequest:React.FC = () => {
  const [positions, setPositions] = useState<Position[]>([]);
  const [userName, setUserName] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const [userPhone, setUserPhone] = useState('');
  const [userPosition, setUserPosition] = useState('');
  const [userPhoto, setPhoto] = useState<File>();
  const [status, setStatus] = useState(false);

  const onReset = () => {
    return (
      setUserName(''),
      setUserEmail(''),
      setUserPhone(''),
      setUserPosition(''),
      setStatus(false)
    );
  };

  useEffect(() => {
    usersApi.getPositions()
      .then((list) => {
        return (
          setPositions(list.positions)
        );
      });
  }, []);

  const handleChangeName = (event: React.ChangeEvent<HTMLInputElement>) => {
    event.preventDefault();
    setUserName(event.target.value);
  };

  const handleChangeEmail = (event: React.ChangeEvent<HTMLInputElement>) => {
    event.preventDefault();
    setUserEmail(event.target.value);
  };

  const handleChangePhone = (event: React.ChangeEvent<HTMLInputElement>) => {
    event.preventDefault();
    setUserPhone(event.target.value);
  };

  const handleChangePhoto = (event: React.ChangeEvent<HTMLInputElement>) => {
    event.preventDefault();
    if (event.target.files) {
      setPhoto(event.target.files[0]);
    }
  };

  const handleChangePosition = (event: React.ChangeEvent<HTMLInputElement>) => {
    event.preventDefault();
    setUserPosition(event.target.value);
  };

  const handleSubmitForm = (event: React.FormEvent) => {
    event.preventDefault();

    if (userPhoto) {
      usersApi.addNewUser(userName, userEmail, userPhone, userPosition, userPhoto)
        .then(response => {
          setStatus(response.success);
        });

      setTimeout(() => {
        onReset();
      }, 5000);
    }
  };

  return (
    <div className="new-user">
      <h1 className="new-user__title">Working with POST&nbsp;request</h1>
      {!status ? (
        <form
          className="new-user__form form"
          onSubmit={handleSubmitForm}
        >
          <input
            type="text"
            placeholder="Your Name"
            className="form__text input"
            value={userName}
            onChange={handleChangeName}
            minLength={2}
            maxLength={60}
            required
          />
          <input
            type="email"
            placeholder="Email"
            className="form__email input"
            value={userEmail}
            onChange={handleChangeEmail}
            minLength={2}
            maxLength={100}
            required
          />

          <input
            type="phone"
            placeholder="Phone"
            className="form__phone input"
            value={userPhone}
            onChange={handleChangePhone}
            required
            pattern="[+]{1}380[0-9]{9}"
            size={13}
          />
          <p className="form__phone-description">+ 38 (XXX) XXX - XX - XX</p>

          <p className="form__title">Select your position</p>
          {positions.map((position: Position) => {
            const { name, id } = position;

            return (
              <div className="form__position" key={id}>
                <input
                  id={name}
                  type="radio"
                  name={name}
                  value={id}
                  className="radio"
                  onChange={handleChangePosition}
                />
                <label className="radio__label" htmlFor={name}>{name}</label>
              </div>
            );
          })}

          <div className="form__file field-file">
            <input
              id="file"
              type="file"
              name="Upload your photo"
              className="field-file__input"
              accept=".jpeg, .jpg"
              onChange={handleChangePhoto}
              required
            />
            <label htmlFor="file" className="field-file__label">
              Upload
            </label>
            {userPhoto
              ? <span className="field-file__label--text uploaded">{userPhoto.name}</span>
              : <span className="field-file__label--text">Upload your photo</span>}
          </div>

          {userName !== '' && userEmail !== '' && userPhone !== '' && userPosition !== '' && userPhoto ? (
            <button type="submit" className="form__button button">Sign up</button>
          ) : (
            <button type="button" className="form__button button button--freezed">Sign up</button>
          )}
        </form>
      ) : (
        <img
          src={newImage}
          alt="Successfully registered"
          className="form__img"
        />
      )}
    </div>
  );
};
