import React from 'react';
import { Tooltip, TooltipProps, tooltipClasses } from '@mui/material';
import { styled } from '@mui/material/styles';
import './UserInfo.scss';

type Props = {
  user: User
};

const CustomTooltip = styled(({ className, ...props }: TooltipProps) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(() => ({
  [`& .${tooltipClasses.tooltip}`]: {
    fontFace: 'Nunito',
    fontSize: '16px',
    lineHeight: '32px',
    borderRadius: '4px',
    backgroundColor: 'rgba(0,0,0,0.87)',
  },
}));

// eslint-disable-next-line react/function-component-definition
export const UserInfo:React.FC<Props> = ({ user }) => {
  const description = `${user.name} photo`;

  return (
    <div className="user">
      <img src={user.photo} alt={description} className="user__photo" />
      <div className="user__info">
        <CustomTooltip title={user.name} placement="bottom-end">
          <p className="user__name">{user.name}</p>
        </CustomTooltip>
        <p className="user__position">{user.position}</p>
        <CustomTooltip title={user.email} placement="bottom-end">
          <p className="user__email">{user.email}</p>
        </CustomTooltip>
        <p className="user__phone">{user.phone}</p>
      </div>
    </div>
  );
};
