const BASE_URL = 'https://frontend-test-assignment-api.abz.agency/api/v1';

export function getUsers(page:number):Promise<any> {
  return fetch(`${BASE_URL}/users?page=${page}&count=6`)
    .then(response => response.json());
}

export function addUsers(link:string):Promise<any> {
  return fetch(link)
    .then(response => response.json());
}

export function getPositions():Promise<any> {
  return fetch(`${BASE_URL}/positions`)
    .then(response => response.json());
}

export async function addNewUser(
  name:string,
  email:string,
  phone:string,
  positionId:string,
  photo:File,
) {
  const formData = new FormData();
  const tokenResponse = await fetch(`${BASE_URL}/token`);
  const newToken = await tokenResponse.json();

  formData.append('position_id', positionId);
  formData.append('name', name);
  formData.append('email', email);
  formData.append('phone', phone);
  formData.append('photo', photo);

  if (newToken.success === true) {
    return fetch('https://frontend-test-assignment-api.abz.agency/api/v1/users', {
      method: 'POST',
      body: formData,
      headers: {
        Token: newToken.token,
      },
    })
      .then(response => response.json());
  }

  throw new Error(newToken.success);
}
