/* eslint-disable react/function-component-definition */
import './GetRequest.scss';
import { UserInfo } from '../UserInfo';

type Props = {
  users: User[],
  handleClick: () => void,
  nextLink: string,
};

export const GetRequest: React.FC<Props> = ({ handleClick, users, nextLink }) => {
  return (
    <div className="users">
      <h1 className="users__title">Working with GET&nbsp;request</h1>
      <ul className="users__list">
        {users.map((user) => {
          return (
            <UserInfo user={user} key={user.id} />
          );
        })}
      </ul>
      <button
        type="button"
        className={nextLink ? 'users__button button' : 'users__button--disabled'}
        onClick={handleClick}
      >
        Show more
      </button>
    </div>
  );
};

export default GetRequest;
